import { request } from 'popsicle';
import { Promise } from 'es6-promise';

export class SeatersClient {

  constructor () {

  }

  greet (name: string): string {
    return 'Hello, ' + name;
  }

  getAppEnv (): Promise<any> {
    return request('/api/app/env');
  }

}



